.property .details {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.property .content-wrapper {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.property .content-wrapper section {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.property .content-wrapper section:first-child {
    width: 100%;
}

.property .details .image {
    position: relative;
    border-radius: 8px 8px 0px 0px;
    overflow: hidden;
    height: 250px;
}

.property .details .image img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.property .details .image span {
    position: absolute;
    top: 8px;
    left: 8px;
    background-color: #878DA4CC;
    padding: 2px 16px;
    border-radius: 16px;
    font-size: 14px;
    font-weight: 500;
    color: #fff;
}

.property .details .header {
    padding: 0rem 1rem;
    display: flex;
    gap: 1rem;
    justify-content: space-between;
    flex-wrap: wrap;
}

.property .details .header li:first-child h3,
.property .details .header li:last-child span {
    font-size: 18px;
    color: #000;
    font-weight: 500;
    text-transform: capitalize;
}

.property .details .header li:first-child span {
    font-size: 12px;
    font-weight: 500;
    text-decoration: underline;
    text-transform: capitalize;
    color: #0084D1;
}

.property .details .services {
    display: flex;
    align-items: center;
    padding: 0rem 1rem;
    gap: 2rem 3rem;
    flex-wrap: wrap;
}

.property .details .services li {
    width: 40px;
    height: 40px;
    overflow: hidden;
}

.property .details .services li img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.property .details p {
    padding: 0rem 1rem;
    margin-top: 1rem;
    font-size: 14px;
    font-weight: 400;
    color: #000
}

.property .images {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    position: relative;
    margin: 0rem auto;
}

.property .images li {
    height: 150px;
    max-width: 250px;
    overflow: hidden;
    border-radius: 8px;
}

.property .images li img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.property .images span {
    position: absolute;
    padding: 2px 6px;
    background-color: #878DA4CC;
    color: #fff;
    display: flex;
    gap: 6px;
    align-items: center;
    border-radius: 4px;
    font-size: 12px;
    bottom: 0px;
    right: 0px;
}

.property .enquire {
    padding: 1rem;
    border-radius: 24px;
    border: 1px solid #eeeeee;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
}

.property .enquire input {
    border: 1px solid #eeeeee;
    color: #878DA4;
}

.property .enquire p {
    padding: 1rem;
    border: 1px solid #eeeeee;
    border-radius: 8px;
    color: #585858;
}


@media only screen and (min-width:768px) {
    .property .details .image {
        height: 400px;
    }
}

@media only screen and (min-width:1024px) {
    .property .content-wrapper {
        flex-direction: row;
    }

    .property .images {
        grid-template-columns: 1fr;
        width: max-content;
    }

    .property .images li {
        height: 200px;
    }
}