.container .wrapper.financial-hero {
    background: linear-gradient(180deg, #dcbff1 0%, rgba(255, 255, 255, 0) 108.18%);
    padding-bottom: 4rem;
    overflow: hidden;
    height: 1000px;
}

.financial-hero-background::before {
    /* min-width: 1400px; */
    position: absolute;
    content: "";
    top: 250px;
    opacity: 0.7;
    overflow: hidden;
    width: 100%;
    left: 0rem;
    height: 600px;
    width: 100%;
    z-index: 3;
    background: url('../../../Assets/Images/city-bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 600px;
}

.financial-hero-background::after {
    /* min-width: 1400px; */
    position: absolute;
    width: 100%;
    left: 0%;
    height: 600px;
    top: 350px;
    content: "";
    z-index: 2;
    background: url('../../../Assets/Images/city-bg.png');
    opacity: 0.1;
    transform: matrix(1, 0, 0, -1, 0, 0);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 600px;
}

.financial-data-container {
    margin-top: -350px;
    position: relative;
    z-index: 5;
}

@media only screen and (min-width : 768px) {
    .financial-hero::after {
        top: 300px;
    }

    .financial-hero::before {
        top: 200px;
    }

    .container .wrapper.financial-hero {
        height: 800px;
    }

    .financial-data-container {
        margin-top: -400px;
    }
}

.financial-hero .services-tabs {
    display: grid;
    grid-template-columns: 1fr;
    gap: 16px;
    position: relative;
    z-index: 4;
}

@media only screen and (min-width:768px) {
    .financial-hero .services-tabs {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media only screen and (min-width:1024px) {
    .financial-hero .services-tabs {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

.financial-hero .services-tabs li {
    width: 100%;
    background-color: #fff;
    padding: 1rem;
    border-radius: 1rem;
    padding: 1rem;
    box-shadow: 4px 4px 24px rgba(0, 0, 0, 0.08);
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    align-items: center;
}

.financial-hero .services-tabs li h3 {
    font-size: 20px;
    line-height: 20px;
    color: #666666;
    font-weight: 500;
}

.financial-hero .services-tabs li:first-child h3 {
    color: #AB4618;
}

.financial-hero .services-tabs li:first-child div {
    background: url('../../../Assets//Images/realestateGrey.svg');
    width: 64px;
    height: 64px;
}

.financial-hero .services-tabs li:nth-child(2) div {
    background: url('../../../Assets//Images/Financialservices.svg');
    width: 64px;
    height: 64px;
}

.financial-hero .services-tabs li:last-child div {
    background: url('../../../Assets//Images/medicalGrey.svg');
    width: 64px;
    height: 64px;
}

.financial-search {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    margin-top: 3rem;
    z-index: 4;
    position: relative;
}

.financial-search h2 {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: #272727;
}

.financial-search .search-bar {
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: center;
}

.financial-search .search-bar .search-bar-inputs {
    display: flex;
    align-items: center;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.04), 0px 4px 16px rgba(0, 0, 0, 0.05);
    border-radius: 100px;
}

.financial-search .search-bar .search-bar-inputs select {
    background-color: #fff;
    height: 48px;
    border-radius: 100px 0px 0px 100px;
    padding: 0rem 32px 0px 32px;
    font-size: 14px;
    font-weight: 500;
    color: #272727;
    -moz-appearance: none;
    -webkit-appearance: none;
    min-width: 80px;
}

.financial-hero .search-bar .search-bar-inputs .location-select select {
    border-radius: 0px 100px 100px 0px;
    border-left: 1px solid #e1e4e5;
    color: #9496A0;
}

.financial-hero .search-bar .search-bar-inputs .search-select {
    position: relative;
}

.financial-hero .search-bar .search-bar-inputs .search-select .down-icon {
    position: absolute;
    right: 12px;
    top: 50%;
    color: #272727;
    transform: translateY(-50%);
}

.financial-hero .search-bar .search-bar-inputs .search-select .location-icon {
    position: absolute;
    left: 10px;
    top: 50%;
    color: #272727;
    transform: translateY(-50%);
}

@media only screen and (min-width: 768px) {
    .financial-search .search-bar {
        background-color: #fff;
        padding: 1px;
        box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.04), 0px 4px 16px rgba(0, 0, 0, 0.05);
        flex-direction: row;
        gap: 0px;
        border-radius: 100px;
        overflow: hidden;
    }

    .financial-search .search-bar .search-bar-inputs {
        box-shadow: none;
        border-radius: 0px;
        align-items: center;
        background-color: #fff;
    }

    .financial-search .search-bar .search-bar-inputs select {
        padding: 0rem 3rem;
    }

    .financial-search .search-bar button {
        margin-left: 6rem;
    }
}

.financial-plans .plans {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
}

@media only screen and (min-width: 768px) {
    .financial-plans .plans {
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (min-width: 1024px) {
    .financial-plans .plans {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

.financial-plans .content-wrapper p {
    margin-bottom: 2rem;
}

.financial-plans .plans .plan {
    background-color: #fff;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    padding: 2rem;
    display: flex;
    gap: 2rem;
    flex-direction: column;
}

.financial-plans .plans .plan h3 {
    text-align: center;
    font-size: 24px;
    color: #272727;
    font-weight: 500;
    text-transform: capitalize;
}

.financial-plans .plans .plan ul {
    display: flex;
    flex-direction: column;
    gap: 8px;
}


.financial-plans .plans .plan ul li {
    position: relative;
    padding-left: 24px;
    font-size: 18px;
    font-weight: 400;
    color: #585858
}

.financial-plans .plans .plan ul li svg {
    position: absolute;
    left: 0px;
    top: 5px;
    background: rgba(124, 213, 24, 0.2);
    color: #88D92F;
    /* border: 2px solid #7CD518; */
    width: 16px;
    background-color: #E3F3CE;
    height: 16px;
    border-radius: 50%;
}

.financial-plans .plans .plan button {
    margin: 0rem auto;
    display: block;
    margin-top: 1rem;
}