.modal-container .modal .content form .image-box input,
.admin-container .profile form .image-box input {
    display: none;
}

.admin-auth-container {
    width: 100vw;
    padding: 2rem;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(180deg, #E0F2FF 12.38%, #FFFFFF 99.97%);
    position: relative;
    box-sizing: border-box;
    overflow: hidden;
}

.admin-auth-container::before {
    position: absolute;
    content: "";
    bottom: -40px;
    overflow: hidden;
    width: 100%;
    left: 0rem;
    height: 600px;
    opacity: 0.7;
    width: 100%;
    z-index: 3;
    background: url('./Assets/Images/city-bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 600px;
}

.admin-auth-container::after {
    position: absolute;
    width: 100%;
    left: 0%;
    height: 600px;
    bottom: -140px;
    content: "";
    z-index: 2;
    background: url('./Assets/Images/city-bg.png');
    opacity: 0.1;
    transform: matrix(1, 0, 0, -1, 0, 0);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 600px;
}

.admin-auth-container .auth-box {
    background-color: rgba(166, 173, 201, 0.5);
    max-width: 450px;
    width: 100%;
    padding: 3rem 2rem;
    position: relative;
    z-index: 4;
    border-radius: 1rem;
}

.admin-auth-container .auth-box form {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
}

.admin-auth-container p {
    font-size: 32px;
    line-height: 32px;
    font-weight: 500;
    color: #272727;
    margin-bottom: 2rem;
}

.admin-container .profile {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.admin-container .profile .info {
    height: max-content;
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.1), 0px 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.admin-container .profile .profile-edit {
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0px 4px 20px rgba(158, 158, 158, 0.2);
}

@media only screen and (min-width: 768px) {
    .admin-container .profile {
        flex-direction: row;
    }

    .admin-container .profile .info {
        max-width: 300px;
    }

    .admin-container .profile .profile-edit {
        width: 100%;
    }
}

.admin-container .profile .info div {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    overflow: hidden;
    background-color: #F5F7F8;
}

.admin-container .profile .info h5 {
    font-size: 20px;
    font-weight: 500;
    color: #000;
}

.admin-container .profile .info p {
    font-size: 14px;
    color: #585858;
    font-weight: 500;
}

.admin-container .profile .info div img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}